.aside {
  width: 300px;
  margin-left: 20px;
  padding-left: 20px;
  border-left: 1px solid #eee;
  &__search {
    position: relative;
    margin-bottom: 40px;
  }

  &__search-input {
    padding: 6px 45px 6px 18px;
    border: 1px solid rgba(#000000, .7);
    border-radius: 20px;
    display: block;
    width: 80%;

    &::placeholder {
      font-family: 'Open Sans', sans-serif;
      font-size: 14px;
      font-weight: 400;
      line-height: normal;
      color: #5A5A5A;
    }
  }

  &__search-btn {
    border: none;
    cursor: pointer;
    background-color: transparent;
    position: absolute;
    right: 0px;
    top: 5px;
  }
}

.blog {
  padding: 60px 100px;
 &__inner {
  display: flex;
  justify-content: space-between;
 }
 &__category {
  margin-bottom: 36px;
 }
 &__category-title {
  margin-bottom: 15px;
  font-family: 'Montserrat';
  font-weight: 700;
  font-size: 16px;
  line-height: 23px;
  color: #585858
}
&__category-item {
  margin-bottom: 12px;
}
&__category-link {
  color: inherit;
}
&__tags-title {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 23px;
  color: #363838;
}
&__tags-list {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-wrap: wrap;
}
 &__tags-link {
   color: #8F8D8D;
   font-weight: 600;
   font-size: 16px;
   text-align: left;
   padding-top: 5px;
   padding-left: 10px;
   padding-right: 10px;
   padding-bottom: 5px;
   margin-bottom: 10px;
   margin-right: 10px;
   display: inline-block;
   border: 1px solid rgba(#454647, .6);
   border-radius: 14px;
 }
 &__social {
  display: flex;
 }
 &__social-item {
  margin-right: 20px;
 }
 &__items {
  max-width: 730px;
 }
 &__item {
  margin-bottom: 100px;
 }
 &__item-img {
  max-width: 730px;
 }
 &__item-info {
  padding: 25px 0 10px;
  font-weight: 600;
  font-size: 14px;
  color: #707070;
  a {
    color: inherit;
  }
 }
 &__item-title {
  font-size: 24px;
  line-height: 38px;
  margin-bottom: 6px;
  color: #363838;
 }
 &__item-text {
  font-weight: 400;
  font-size: 16px;
  line-height: 27px;
  color: #5A5A5A;
 }
 &__blockquote {
  font-size: 24px;
  line-height: 38px;
  font-style: italic;
  font-weight: 300;
  text-align: center;
  background: rgba(223, 223, 223, 0.5);
  margin-bottom: 100px;
  margin-top: 80px;
  padding: 54px 64px;
 }
 &__blockquote-line {
  display: inline-block;
  background-color: #363838;
  text-align: center;
  height: 1px;
  width: 200px;
 }
 &__blockquote-author {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
 }
 &__item-popup {
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
 }
 &__item-img {
  width: 730px;
  height: 558px;
 }
}

.article-popup {
  &__box {
    position: relative;
  }

}

.article-slider {
  &__arrow {
    padding: 0;
    border: none;
    outline: none;
    background-color: transparent;
    cursor: pointer;
    position: absolute;
    transform: translateY(-50%);
    top: 200px;
    z-index: 10;
  }
  &__arrowLeft {
    left: 16px;
  }
  &__arrowRight {
    right: 16px;
  }
}

.recent {
  &__posts-title {
    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 16px;
    color: #363838;
  }
  &__posts-item {
    margin-bottom: 11px;
  }
  &__posts-item-title {
    display: block;
    color: inherit;
  }
  &__posts-date, 
  &__posts-author {
    color: #a1a1a1;
    font-size: 14px;
  }
}

.blog {
  &__item-img {
    width: 100%;
    object-fit: fill;
  }
  &__item-date {
    color: inherit;
  }
  &__item-author {
    color: inherit;
  }
}
.pagination {
  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0 20px;
  }
  &__list-num {
    color: inherit;
  }
}